:host {
  display: block;
  line-height: 18px;
  font-family: 'Prelo';

  .container {
    border: 1px solid #eee;
    border-radius: 2px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    background-color: #ffffff;
  }

  cdn-paginator {
    margin-top: 8px;
  }

  table  {
    width: 100%;

    thead {
      border-radius: 8px 8px 0px 0px;
    }

    th, td {
      text-align: left;
      padding: 4px;
      height: 35px;
      color: #0F265C;
      font-size: 14px;
      padding-left: 20px;
      padding-right: 20px;
    };

    th {
      font-weight: 700;
      border-bottom:1px solid #DEE3ED;
    }

    tr {
      &:hover {
        background-color: #F4F6F9;
      }
    }

    td {
      font-weight: 500;
    }
  }
}
